/**
 * redux
 */


import React, {useState, useEffect, Fragment} from 'react';

function App(props) {
    let [count, setCount] = useState(0);
    let [isClient, setIsClient] = useState(false);
    const onCount = event => {
        setCount(++count);
    };

    useEffect(() => {
        setIsClient(true);
    }, []);

    return (
        <Fragment>
           <div>hello world!</div>
            {count}
           <div>
                <button onClick={onCount}></button>
           </div>
           {
            isClient ? (
                <div>in client</div>
            ) : null
           }
        </Fragment>
    );
};

export default App;